@tailwind base;
@tailwind components;
@tailwind utilities;

/* Custom global styles */
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #f0f4f8; /* Light gray background */
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

h1, h2, h3, h4, h5, h6 {
  color: #1e3a8a; /* Dark blue for headings */
}

button {
  transition: background-color 0.3s ease;
}

button:hover {
  background-color: #2563eb; /* Slightly darker blue on hover */
}

textarea, input {
  background-color: #f9fafb; /* Light background for form inputs */
  border: 1px solid #d1d5db; /* Light gray border */
}

textarea:focus, input:focus {
  border-color: #2563eb; /* Blue border on focus */
  outline: none;
  box-shadow: 0 0 0 3px rgba(37, 99, 235, 0.3); /* Subtle shadow on focus */
}

/* Responsive design tweaks */
@media (min-width: 768px) {
  .container {
    max-width: 700px;
    margin: 0 auto;
  }
}
